.ad-sizer {
  position: relative;
}
.ad-sizer .goodloopad,
.ad-sizer iframe {
  position: absolute;
  top: 0;
  left: 0;
}
.ad-sizer .vpaid-container {
  width: 100%;
  height: 100%;
  position: absolute;
}
.ad-sizer.landscape .aspectifier {
  margin-top: 56.25%;
}
.ad-sizer.portrait .aspectifier {
  margin-top: 177.77%;
}
.ad-sizer.square .aspectifier {
  margin-top: 100%;
}
.ad-sizer.fabric {
  margin: auto -16px;
  border: 1px solid transparent;
}
.ad-sizer.fabric .aspectifier {
  margin-top: 250px;
}
.ad-sizer.portrait,
.ad-sizer.landscape,
.ad-sizer.square {
  overflow: hidden;
}
#frame-sizer,
#controls {
  display: inline-block;
  vertical-align: top;
  margin: 2vh;
}
#controls {
  width: 25em;
}
#frame-sizer.aspect-4_3 {
  font-size: 1.15vh;
}
#frame-sizer.aspect-4_3 #phone-screen {
  height: 60em;
}
#frame-sizer.aspect-16_9 {
  font-size: 0.9vh;
}
#frame-sizer.aspect-16_9 #phone-screen {
  height: 80em;
}
#frame-sizer.aspect-18_9 {
  font-size: 0.85vh;
}
#frame-sizer.aspect-18_9 #phone-screen {
  height: 90em;
}
#phone-frame {
  background-color: silver;
  padding: 0;
  border-radius: 5em;
  display: inline-block;
  text-align: center;
}
.phone-speaker {
  background-color: white;
  display: block;
  margin: 1em auto;
  width: 20em;
  height: 2em;
  border-radius: 1em;
}
#phone-screen {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 1px solid black;
  margin: 0 2em;
  width: 45em;
  border-radius: 2em;
  background-color: white;
  overflow: hidden;
}
#phone-screen .goodloopad {
  border: none;
  height: 100%;
}
#phone-screen .bar {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
#phone-screen .bar .content {
  font-family: sans-serif;
}
#screen-content {
  width: 100%;
  height: 100%;
}
#screen-content #external-video {
  height: 50%;
  position: relative;
}
#screen-content #external-video video {
  height: 100%;
  width: 100%;
  background-color: black;
}
#screen-content #external-video .ext-video-label {
  position: absolute;
  height: 10%;
  width: 100%;
  color: white;
  top: 45%;
  font-size: 250%;
  font-weight: bold;
}
#screen-content.extVideo .goodloopad {
  height: 50%;
}
#status-bar {
  background-color: black;
  color: white;
  height: 2.8em;
}
#status-bar .content {
  font-size: 200%;
}
#address-bar {
  background-color: silver;
  border-bottom: 1px solid black;
  height: 6.4em;
}
#address-bar .content {
  font-size: 250%;
  border-radius: 1em;
  background-color: white;
  display: inline-block;
  width: 90%;
  text-align: center;
  padding-left: 1em;
  margin: 0.5em auto;
}
#navigation-bar {
  background-color: black;
  color: white;
  height: 6.4em;
}
#navigation-bar .content {
  font-size: 450%;
  display: flex;
  justify-content: space-evenly;
}
.navbar-brand {
  height: 2em;
}
.navbar-brand img {
  height: 100%;
}
.test-ad-selector {
  display: flex;
  justify-content: space-between;
  padding: 1em 0.5em;
}
.test-ad-selector .dropdown-toggle {
  font-size: 1.3em;
  background-color: #830000;
}
.test-ad-selector .dropdown-menu > *:hover {
  text-decoration: none;
}
.test-ad-selector .dropdown-item:hover {
  background-color: #830000;
  color: white;
}
.test-ad-selector .selector-info {
  font-weight: 600;
  display: flex;
  align-items: center;
}
.ad-sizer.portrait {
  max-width: 400px;
  margin: 0 auto;
}
